.App {
  position: relative;
  /* height: 100vh; */
  overflow: hidden;
}

:root {
  --index: 1vw + 1vh;
  --main-light-color: #f9bbb1;
  --main-dark-color: #481b0b;
  --main-background-color: black;
}

.background_fone {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: calc(var(--index) * 220);
}

.bubble {
  --r: 25px; /* the radius */
  --t: 30px; /* the size of the tail */
  max-width: 300px;
  padding: calc(2 * var(--r) / 3);
  -webkit-mask: radial-gradient(var(--t) at var(--_d) 0, #0000 98%, #000 102%)
      var(--_d) 100% / calc(100% - var(--r)) var(--t) no-repeat,
    conic-gradient(at var(--r) var(--r), #000 75%, #0000 0) calc(var(--r) / -2)
      calc(var(--r) / -2) padding-box,
    radial-gradient(50% 50%, #000 98%, #0000 101%) 0 0 / var(--r) var(--r) space
      padding-box;
  background: linear-gradient(135deg, #fe6e00, #1384c5) border-box;
  color: #fff;
  z-index: 3;
  opacity: 0.9;
}
.left {
  --_d: 0%;
  position: absolute;
  top: 22%;
  left: 75%;
  width: max-content;
  max-width: 210px;
  border-left: var(--t) solid #0000;
  margin-right: var(--t);
  place-self: start;
  transform: scaleY(-1);
}

.right {
  --_d: 100%;
  position: absolute;
  bottom: 79%;
  right: 65%;
  width: max-content;
  max-width: 205px;
  border-right: var(--t) solid #0000;
  margin-left: var(--t);
  place-self: end;
}



.flare {
  position: absolute;
  top: 0;
  height: 99%;
  width: 25%;
  transform: skewX(-45deg);
  animation: flareAnimation linear 3s infinite;
  /* background: linear-gradient(90deg, rgba(255, 255, 255, 0.1), #1384c598); */
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.1),
    rgba(255, 111, 0, 0.55) 75%,
    rgba(255, 111, 0, 0.15)
  );
  /* background: linear-gradient(90deg, red 0%, orange 25%, yellow 50%, green 75%, blue 100%); */
  /* background: linear-gradient(red 10%, 30%, blue 90%); */
  overflow-x: hidden;
  /* box-shadow: 0 0 10px #fe6e008a; */
}
@keyframes flareAnimation {
  0% {
    left: -150%;
  }
  70% {
    left: -50%;
    width: 40%;
  }
  100% {
    width: 100%;
    left: 150%;
  }
}

@media (max-width: 1300px) {
  .background_fone {
    /* max-width: 150%; */
    max-width: calc(var(--index) * 180);
    left: -5vw;
  }
}
@media (max-width: 1000px) {
  .left,
  .right {
    font-size: 13px;
  }
}
@media (max-width: 740px) {
  .background_fone {
    max-width: 170%;
    left: -12vw;
  }

  .left {
    left: 65%;
  }
  .right {
    right: 55%;
  }
}
