.steps_fone {
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  position: relative;
}
.window,
.window_2 {
  background-color: white;
  border-radius: 10px;
  padding: calc(var(--index));
  padding-top: calc(var(--index) * 5);
  z-index: 4;
  min-width: 40vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 95vw;
  position: relative;
}
.variants_block,
.variants_block_1 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1vw;
  overflow-y: auto;
  height: max-content;
  width: 100%;
}

.variants_block_1 {
  grid-template-columns: repeat(2, 1fr);
}
.current_variant {
  padding: calc(var(--index)) calc(var(--index) * 0.1);
  border: 1px solid black;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  transition: 0.7s;
  min-height: 100px;
  max-height: 250px;
}
.current_variant span {
  text-align: center;
  font-weight: 500;
}
.element_is_checked {
  transition: 0.7s;
  color: green;
  font-weight: bold;
}
.buttons{
  display: flex;
  gap: 2vw;
}
.buttons button {
  margin-top: 20px;
  cursor: pointer;
  border: none;
  padding: 10px 30px;
  background: linear-gradient(135deg, #fe6d00, #1384c5) border-box;
  border-radius: 10em;
  font-size: calc(var(--index));
  color: white;
  box-shadow: 2px 2px 3px black;
}
.current_result {
  padding: calc(var(--index) * 2);
  border-radius: 10px;
  border: 1px solid black;
  text-align: center;
  cursor: pointer;
  transition: 0.3s;
}
.clouse {
  position: absolute;
  font-size: 20px;
  font-weight: bold;
  top: 15px;
  right: 20px;
  cursor: pointer;
}
.current_result {
  padding: calc(var(--index) * 0.5);
}
.result_list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1vh;
  overflow-y: auto;
}
.opened_current_result {
  overflow-y: auto;
  max-height: 100%;
  max-width: fit-content;
}
.opened_current_result img {
  max-width: 100%;
}
.opened_current_result div h3 {
  max-width: 50vw;
}
.current_result h3 {
  font-size: 14px;
}
@media (max-width: 870px) {
  .opened_current_result div h3 {
    max-width: 90vw;
  }
  .variants_block,
  .variants_block_1 {
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
  }
  .result_list {
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
  }
}
@media (max-width: 470px) {
  .variants_block,
  .variants_block_1 {
    grid-template-columns: repeat(1, 1fr);
  }
  .result_list {
    grid-template-columns: repeat(1, 1fr);
    min-width: 85vw;
  }
}
