.persons_style {
  position: absolute;
  bottom: 10px;
  display: flex;
  gap: 10vw;
  justify-content: center;
  width: 100%;
}

.welcome {
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 10;
}
.welcome_central_container {
  color: white;
  padding: calc(var(--index));
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
.welcome_central_container h1 {
  font-size: calc(var(--index));
  color: black;
}

.menu_component {
  position: absolute;
  top: calc(var(--index) * 2);
  left: calc(var(--index) * 2);
  padding: calc(var(--index));
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
  gap: 0.5vh;
  z-index: 4;
  transition: 0.7s;
}
.menu_component div,
.menu_component section {
  background-color: white;
  font-size: calc(var(--index));
  border-radius: 5px;
  padding: calc(var(--index) * 0.01) calc(var(--index));
  text-align: center;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}
.menu_component div {
  position: relative;
  overflow: hidden;
}
.menu_component div span {
  top: 2px;
  right: 2px;
  width: calc(var(--index) * 0.15);
  height: calc(var(--index) * 0.15);
  display: block;
  border-radius: 50%;
  position: absolute;
}

.dialog_component {
  position: absolute;
  bottom: calc(var(--index) * 2);
  right: calc(var(--index) * 2);
  z-index: 2;
  max-width: 450px;
}
.current_q {
  animation: puls linear 1.5s infinite;
}
@keyframes puls {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
.patient_wrapper {
  position: relative;
}
.doctor_wrapper {
  position: relative;
}
.questions_container {
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.questions_container h2 {
  font-size: calc(var(--index));
  margin: 0 auto;
}
.questions_container div {
  padding: 5px 15px;
  font-size: 15px;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
}
.dialog_history {
  width: 0;
  background-color: black;
  position: fixed;
  top: 0;
  right: 0;
  transition: 0.7s;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  z-index: 10;
}
.current_dialog_wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.alert_wrapper {
  width: 100%;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  gap: 20px;
}
.alert_wrapper img {
  max-width: 200px;
}
.attantion {
  font-size: calc(var(--index) * 10);
  width: calc(var(--index) * 15);
  height: calc(var(--index) * 15);
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: 1px solid white;
  color: white;
  border-radius: 50%;
}
.alert_message {
  font-size: 18px;
  font-weight: 700;
  text-align: center;
}
.say_hello {
  background-color: rgba(128, 128, 128, 0.45);
  padding: 5px;
  border-radius: 5px;
}
.general_information {
  background-color: rgba(128, 128, 128, 0.2);
  padding: 5px;
  border-radius: 5px;
}
.life_history {
  background-color: rgba(128, 128, 128, 0.45);
  padding: 5px;
  border-radius: 5px;
}
.result_of_treatmet_dialog {
  background-color: rgba(128, 128, 128, 0.2);
  padding: 5px;
  border-radius: 5px;
}
.say_hello h3,
.general_information h3,
.life_history h3,
.result_of_treatmet_dialog h3 {
  color: grey;
  text-decoration: underline;
}

.say_hello div div:nth-child(1),
.general_information div div:nth-child(1),
.life_history div div:nth-child(1),
.result_of_treatmet_dialog div div:nth-child(1) {
  color: white;
}
.say_hello div div:nth-child(1) span,
.general_information div div:nth-child(1) span,
.life_history div div:nth-child(1) span,
.result_of_treatmet_dialog div div:nth-child(1) span {
  color: rgb(255, 94, 0);
  font-size: 18px;
  font-weight: bold;
}
.say_hello div div:nth-child(2),
.general_information div div:nth-child(2),
.life_history div div:nth-child(2),
.result_of_treatmet_dialog div div:nth-child(2) {
  color: white;
}
.say_hello div div:nth-child(2) span,
.general_information div div:nth-child(2) span,
.life_history div div:nth-child(2) span,
.result_of_treatmet_dialog div div:nth-child(2) span {
  color: rgb(217, 255, 0);
  font-size: 18px;
  font-weight: bold;
}
.hint_component div {
  max-width: 700px;
}
.hint_component img {
  max-width: 100%;
}
.hint_component li {
  margin: 0 0 0 20px;
}
.clouse_open_menu {
  position: absolute;
  bottom: -15%;
  right: 0;
}

.container {
  position: absolute;
  z-index: 4;
  left: 0;
  right: 0;
  margin: 8vh auto;
  width: max-content;
  padding: 20px 40px;
  background: linear-gradient(135deg, #fe6e00, #1384c5) border-box;
  color: white;
  border-radius: 5px;
  display: flex;
  align-items: center;
  animation: linear 1s puls infinite;
  cursor: pointer;
}
@keyframes puls {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

@media (max-width: 1100px) {
  .dialog_component {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100vw;
    max-width: none;
    z-index: 2;
    max-height: 20vh;
  }
}

@media (max-width: 970px) {
  .menu_component {
    top: 0;
    left: 0;
    border-radius: 0 0 10px 10px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
  .persons_style div img {
    max-width: 30vw;
  }
  .container {
    margin: 22vh auto;
  }
}

@media (max-width: 780px) {
  .persons_style div img {
    max-width: 40vw;
  }
  .persons_style {
    gap: 10vw;
  }

  .alert_wrapper {
    width: 90vw;
  }
  .menu_component div span {
    width: calc(var(--index));
    height: calc(var(--index));
  }
}
@media (max-width: 650px) {
  .menu_component {
    grid-template-columns: repeat(4, 1fr);
  }
  .welcome_central_container {
    padding: calc(var(--index) * 2);
    max-width: 90vw;
  }
  .welcome_central_container h1 {
    font-size: calc(var(--index) * 2.5);
    text-align: center;
  }
  .container {
    margin: 30vh auto;
  }
}
@media (max-width: 590px) {
  .persons_style div img {
    width: 60vw;
  }
  .persons_style {
    gap: 20vw;
  }
}
@media (max-width: 500px) {
  .menu_component {
    grid-template-columns: repeat(3, 1fr);
  }
  .menu_component div {
    font-size: calc(var(--index) * 1.3);
  }
}
@media (max-width: 430px) {
  .persons_style {
    gap: 27vw;
  }
}
